import {h, Component} from 'preact';
import {defaults, Line} from 'preact-chartjs-2';

defaults.global.animation = false;
defaults.global.tooltips.position = 'nearest';

let initialState = {
  labels: [],
  datasets: [
    {
      label: 'Miners',
      type: 'line',
      data: [],
      fill: false,
      borderColor: '#EC932F',
      backgroundColor: '#EC932F',
      pointBorderColor: '#EC932F',
      pointBackgroundColor: '#EC932F',
      pointHoverBackgroundColor: '#EC932F',
      pointHoverBorderColor: '#EC932F',
      yAxisID: 'y-axis-1'
    },
    {
      label: 'HashRate (TH/s)',
      type: 'line',
      data: [],
      fill: false,
      backgroundColor: '#71B37C',
      borderColor: '#71B37C',
      hoverBackgroundColor: '#71B37C',
      hoverBorderColor: '#71B37C',
      yAxisID: 'y-axis-2'
    },
  ],
};

const options = {
  responsive: true,
  elements: {
    line: {
      fill: false,
      tension: 0
    }
  },
  responsiveAnimationDuration: 0,
  hover: {
    animationDuration: 0,
  },
  tooltips: {
    mode: 'x-axis'
  },
  scales: {
    xAxes: [
      {
        display: true,
        gridLines: {
          display: false
        },
      }
    ],
    yAxes: [
      {
        type: 'linear',
        display: true,
        position: 'left',
        id: 'y-axis-1',
        gridLines: {
          display: false
        },
      },
      {
        type: 'linear',
        display: true,
        position: 'right',
        id: 'y-axis-2',
        gridLines: {
          display: false
        },
      }
    ]
  }
};

function processPoolStats(rawStats) {
  let lastDt = '';
  const dtPoints = rawStats.stats.concat([]).reverse().map(p => {
    const parts = p.created.split('T');
    if (parts[0] !== lastDt) {
      lastDt = parts[0]
      return parts.join(' ').slice(0, -3);
    }
    return parts[1].slice(0, -3);
  });
  const miners = rawStats.stats.map(p => +p.connectedMiners);
  const hashRates = rawStats.stats.map(p => parseInt(p.poolHashRate / 1e12, 10));
  return {
    dtPoints: dtPoints.reverse(),
    miners: miners,
    hashRates: hashRates,
  };
}

function getPoolStats() {
  return new Promise((resolve, reject) => {
    fetch('http://api.notbcash.org/api/pool/bcl0/stats/hourly')
      .then(r => r.json())
      .then(res => {
        // console.log('RES:', res, typeof res);
        if (res) {
          try {
            // const poolStats = res.json();
            resolve(processPoolStats(res));
          } catch (e) {
            console.log('[ERR][getPoolStats][JSON]', e);   
            return reject(e);
          }
        }
      })
      .catch(err => {
        return console.log('[ERR][getPoolStats]', err);
      })
  });
}


class PoolStats extends Component {
  state = initialState; //{ dtPoints: [], miners: [], hashRates: [] };

  componentWillMount() {
    this.refreshData().then(() => {
      this.loopStats();
    });
  }

  // gets called just before navigating away from the route
  componentWillUnmount() {
    clearInterval(this.timer);
    if (this.refs && this.refs.chartpool && this.refs.chartpool.chart_instance) {
      this.refs.chartpool.chart_instance.destroy();
    }
  }

  loopStats() {
    // console.log(new Date(), '---- loopStats');
    this.timer = setTimeout(() => {
      if (document.hidden === false) {
        // console.log(new Date(), 'refreshData - RAF');
        requestAnimationFrame(this.refreshData.bind(this, true));
      } else {
        // console.log(new Date(), 'refreshData - wait visible tab');
        this.loopStats();
      }
    }, 10 * 60 * 1e3);
  }

  refreshData(isLoop) {
    return getPoolStats().then(stats => {
      initialState.labels = stats.dtPoints;
      initialState.datasets[0].data = stats.miners;
      initialState.datasets[1].data = stats.hashRates;
      this.setState(initialState);
      if (isLoop) {
        setTimeout(() => this.loopStats(), 1);
      }
    });
  }

  render({}, state) {
    return (
      <div>
        <h2>Pool Stats</h2>
        <Line
          data={state}
          options={options}
        />
      </div>
    );
  }
};

export default PoolStats;


